.press {
    background: #f3f3f3;

    @include r(350) {
        .pressitem-img {
            width: 80px;
        }
    }

    @include r(1023) {
        .press-header {
            float: none;
            text-align: center;
            margin-bottom: 35px;
            width: 100%;
        }
    
        .press-subtitle {
            margin-top: 5px;
        }
    
        .press-con {
            float: none;
            width: 100%;
        }
    
        ul.press-primary {
            margin-bottom: 15px;
        }
        ul.press-primary > li {
            width: 100%;
            height: 160px;
            margin-bottom: 15px;
        }
    
        ul.press-primary > li:last-child {
            margin-bottom: 0;
        }
    
        .pressitem-img {
            float: left;
            height: 160px;
            width: 160px;
        }
    
        .pressitem-con {
            height: 160px;
            width: auto;
            overflow: hidden;
            padding-left: 20px;
        }
    
        .press-secondary {
            padding: 12px;
        }
    
        .press-secondary > a {
            width: 25%;
        }
    
        .pressitem-media > img {
            height: 35px;
        }
    }
}


.press-header {
    float: left;
    width: 20%;
    text-align: left;
}

.press-con {
    float: right;
    width: 75%;
}

ul.press-primary {
    margin-left: -12px;
    margin-right: -12px;
    margin-bottom: 24px;
}

ul.press-primary > li {
    width: 33.3%;
    float: left;
    padding-left: 12px;
    padding-right: 12px;
}

ul.press-primary > li a:hover .pressitem-desc{
    color: #249faa;
}


.pressitem-img {
    height: 175px;
    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.pressitem-thumb01 {
    background-image: url('../img/media/thumb_01.jpg');
}
.pressitem-thumb02 {
    background-image: url('../img/media/thumb_02.jpg');
}
.pressitem-thumb03 {
    background-image: url('../img/media/thumb_03.jpg');
}

.pressitem-con {
    height: 145px;
    overflow: hidden;
    padding: 10px 20px 20px;
    background: #fff;
}

.pressitem-media > img{
    height: 45px;
}

.pressitem-desc {
    color: #666;
    line-height: 1.4em;
    font-weight: 300;
}
.press-secondary {
    background: #fff;
    overflow: hidden;
    padding: 25px;
    width: 100%;
}

.press-secondary > a {
    float: left;
    width: 16.66%;
    padding: 10px;
}

.press-secondary > a:hover img {
    opacity: 1;
    filter: grayscale(0%);
}

.press-secondary > a > img{
    width: 100%;
    filter: grayscale(100%);
    opacity: 0.5;
    transition: opacity 0.5s,filter 0.5s;
}

.press-subtitle {
    line-height: 1.5em;
    color: #999;
    font-weight: 300;
    margin-top: 25px;
}

footer.main-footer {
    padding: 50px 0;
    text-align:center;
    color: #fff;
    background-color: #445366;
    font-size: 0.9em;
}

footer.main-footer a{
    color: #dbdbdb;
}

footer.main-footer a:hover{
    text-decoration: underline;
}

footer.main-footer .footer-policy a{
    color: #777;
    font-size: 12px;
    text-decoration: none;
}

.footer-policy {
    padding-top: 5px;
}

.rr-logo {
    margin-bottom: 20px;
}

address {
    font-style: normal;
    margin-bottom: 20px;
}

