// variables
  //colors
  $yellow: #fff82d;
  $blue: #31bfd7;
  $dark-blue: #00b1ce;
  $grey: #404853;
  $blue: #00b1ce; //buttons_bg

  // fonts
  $default-fonts: Arial, sans-serif;

// base styles
* {
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', $default-fonts;
  font-size: 18px;
  overflow-x: hidden;
  width: 100vw;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

.clearfix::before, .clearfix::after {
  content:'';
  display:table;
}
.clearfix::after {
  clear:both;
}

.container {
  max-width: 1200px;
  padding: 0 10px;
  margin: 0 auto;
}

.section {
  padding: 60px 0;

  &__heading {
    text-align: center;
    margin-bottom: 60px;
  }

  &__title {
    font-size: 28px;
    color: #282f3a;
    font-weight: 100;

    &_white {
      color: #fff;
    }
  }

  &__subtitle {
    color: #b3bbc8;
    font-size: 18px;
    margin-top: 15px;
  }

  &_pv_size-l {
    padding: 60px 0 140px;
  }

  &_pt_0 {
    padding-top: 0;
  }

  &_bgc_grey {
    background-color: #f7f9fb;
  }

  &_bgc_dark-grey {
    background-color: #445366;
  }

  &_potential {
    background-image: url(../img/potential-bg.png);
    background-repeat: no-repeat;
    background-position: 50% 100%;
    background-size: 100%;
  }

  &_whitepapper {
    background-image: url(../img/whitepapper-bg.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 50%;
  }

  @include rmin(768) {
    padding: 80px 0 110px;
    
    &__title {
      font-size: 48px;
    }

    &_pt_0 {
      padding-top: 0;
    }

    &_pv_size-l {
      padding: 120px 0 140px;
    }
  }
}

.btn {
  font-size: 16px;
  color: #fff;
  text-align: center;
  display: block;
  padding: 13px 40px;
  border: 2px solid #00b1ce;
  background-color: $blue;
  border-radius: 25px;
  font-weight: 400;
  text-decoration: none;
  transition: background-color .3s, color .3s;

  &:hover {
    background-color: #fff;
    color: $blue;
  }

  &_transp {
    background-color: rgba(0,0,0,0);
    border-color: #fff;
  }

  &_ph_20 {
    padding: 13px 20px;
  }
}

.section-home {
  background-image: url(../img/main-bg.png);
  background-size: cover;
  background-position: 50% 50%;
}

.hero {
  text-align: center;
  padding: 80px 0 130px;
  position: relative;
  overflow: hidden;

  &__title {
    font-size: 18px;
    text-transform: uppercase;
    color: #fff;
    font-weight: 300;
    
    br {
      display: none;
    }
  }

  &__timer {
    margin: 45px 0;
  }

  &__timer-title {
    color: #fff;
    font-weight: 400;
    margin-bottom: 20px;
  }

  &__button {
    max-width: 250px;
    margin: 0 auto 45px;
  }

  &__phone {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    max-width: 825px;
    width: 100%;
  }

  &__phone-img {
    display: none;
  }

  &__video-button {
    position: absolute;
    z-index: 2;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
  }

  &__scroll-down {
    margin-bottom: 20px;
    display: none;
  }

  @include rmin(768) {
    padding: 160px 0 260px;

    &__title {
      font-size: 36px;

      br {
        display: inline;
      }
    }

    &__phone-img {
      display: block;
    }

    &__scroll-down {
      display: block;
    }
  }

  @include rmin(1024) {
    min-height: 100vh;

    &__title {
      opacity: 0;
      transform: translateY(-30px);
    }

    &__timer {
      opacity: 0;
      transform: translateY(20px);
    }

    &__button {
      opacity: 0;
      transform: translateY(20px);
    }

    &__phone {
      transform: translate(-50%, 100%);

      .video-btn {
        opacity: 0;
        transition: opacity .3s;
      }

      &.is-active {
        transform: translate(-50%, 0);

        .video-btn {
          opacity: 1;
          transition-delay: 1s;
        }
      }
    }

    &__scroll-down {
      opacity: 0;

      &.is-active {
        opacity: 1;
      }
    }
  }
}

.timer {
  display: flex;
  justify-content: space-between;
  max-width: 300px;
  margin: 0 auto;

  &__item {
    text-align: center;
    padding: 10px 0;
    background-color: rgba(255,255,255,.7);
    border-radius: 5px;
    width: 70px;
    height: 70px;
  }

  &__title {
    font-size: 12px;
    color: #757f92;
    font-weight: 300;
    text-transform: uppercase;
  }

  &__value {
    font-size: 36px;
    color: #464e59;
    font-weight: 300;
  }

  @include rmin(768) {
    max-width: 430px;

    &__item {
      padding: 20px 0;
      width: 100px;
      height: 100px;
    }

    &__value {
      font-size: 48px;
    }
  }
}

.scroll-down {
  padding-bottom: 30px;
  position: relative;
  display: inline-block;

  &__title {
    font-size: 18px;
    color: #fff;
    font-weight: 300;
  }

  &:before {
    content: '';
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    width: 18px;
    height: 18px;
    border: 1px solid #fff;
    border-radius: 50%;
    animation: scrollDownRing 1.5s infinite;
  }

  &:after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: 6px;
    transform: translateX(-50%);
    display: block;
    width: 10px;
    height: 15px;
    background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20viewBox%3D%2225.4%202.2%209%2013.1%22%20enable-background%3D%22new%2025.4%202.2%209%2013.1%22%3E%3Cstyle%20type%3D%22text/css%22%3E.st0%7Bfill%3A%23ffffff%3B%7D%3C/style%3E%3Cpath%20class%3D%22st0%22%20d%3D%22M30%2015.3c-.4%200-.8-.1-1.1-.4l-3.3-3.3c-.2-.2-.2-.5%200-.7.2-.2.5-.2.7%200l3.3%203.3c.2.2.5.2.7%200l3.3-3.3c.2-.2.5-.2.7%200s.2.5%200%20.7l-3.3%203.3c-.2.3-.6.4-1%20.4zM30%2014.7c-.3%200-.5-.2-.5-.5v-12c0-.3.2-.5.5-.5s.5.2.5.5v12c0%20.3-.2.5-.5.5z%22/%3E%3C/svg%3E');
    background-repeat: no-repeat;
    animation: scrollDownArrow 1.5s infinite;
  }
}

@keyframes scrollDownArrow {
  0% {
    transform: translate(-50%, -100%);
    opacity: 0;
  }

  50% {
    transform: translate(-50%, 0%);
    opacity: 1;
  }
  100% {
    transform: translate(-50%, 0%);
    opacity: 1;
  }
}

@keyframes scrollDownRing {
  0% {
    opacity: 1;
    transform: translateX(-50%) scale(1);
  }

  30% {
    opacity: 1;
    transform: translateX(-50%) scale(1);
  }

  100% {
    transform: translateX(-50%) scale(1.5);
    opacity: 0;
  }
}

.video-btn {
  background-color: transparent;
  border: none;
  text-align: center;
  display: inline-block;

  &__title {
    color: #fff;
    font-size: 18px;
    transition: opacity .5s;
  }

  &__icon {
    display: block;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    margin: 10px auto 20px;
    background-color: $blue;
    position: relative;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.3);
    background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20viewBox%3D%220%200%2040%2040%22%20enable-background%3D%22new%200%200%2040%2040%22%3E%3Cpath%20fill%3D%22%23fff%22%20d%3D%22M17.7%2025.5c-1%200-1.7-.8-1.7-1.7v-7.5c0-1%20.8-1.7%201.7-1.7.3%200%20.6.1.9.2l6.5%203.8c.5.3.9.9.9%201.5s-.3%201.2-.9%201.5l-6.5%203.8-.9.1zm0-10c-.4%200-.7.3-.7.7v7.5c0%20.6.7.9%201.1.6l6.5-3.8c.2-.1.4-.4.4-.6s-.1-.5-.4-.6l-6.5-3.8h-.4z%22/%3E%3C/svg%3E');
    background-repeat: no-repeat;
    background-position: 50% 50%;

    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      display: block;
      width: 80px;
      height: 80px;
      border-radius: 50%;
      background-color: rgba(255,255,255,.8);
      z-index: -1;
      transition: transform .5s;
    }
  }

  &:hover {
    .video-btn {

      &__title {
        opacity: .7;
      }

      &__icon {
        &:before {
          transform: translate(-50%, -50%) scale(.7);
        }
      }
    }
    
  }
}

.potential {
  display: flex;
  flex-wrap: wrap;

  &__col {
    width: 100%;

    &:first-child {
      padding-right: 5%;
    }

    & + & {
      display: flex;
      flex-wrap: wrap;
    }

    &_tiles {
      margin-top: 20px;
    }
  }

  &__title {
    margin-bottom: 20px;
  }

  &__text {
    font-size: 16px;
    font-weight: 300;
    color: $grey;
  }

  &__tile {
    padding: 30px 15px;
    box-shadow: 0 3px 10px 0 rgba(0,0,0,.2);
    background-color: #fff;
    width: 100%;
    margin-bottom: 20px;
  }

  &__tile-title {
    font-size: 24px;
    color: $blue;
    font-weight: 300;
    margin-bottom: 25px;
  }

  &__tile-icon {
    height: 35px;
    display: inline-block;
    margin-bottom: 15px;
    font-size: 30px;
    
    .icon {
      fill: $blue;
    }
  }

  @include rmin(768) {
    margin: -10px;

    &__col {
      width: 33.333%;
      padding: 10px;

      &:first-child {
        width: 100%;
      }

      &_tiles {
        margin-top: 0;
      }
    }

    &__tile {
      height: 50%;
      min-height: 230px;
    }
  }
  @include rmin(1024) {
    &__col {
      width: 25%;
      opacity: 0;

      &.is-active {
        opacity: 1;

        .potential__tile {
          opacity: 1;
          transform: translateX(0);
        }
      }

      &:first-child {
        width: 25%;
      }
    }

    &__title {
      margin-bottom: 100px;
    }

    &__tile {
      opacity: 0;
      transform: translateX(20px);
      transition: transform .7s ease-out, opasity .7s ease-out;

      & + & {
        transition-delay: .1s;
      }
    }
  }
}

.about {

  & + & {
    margin-top: 40px;
  }

  &__descr {
    font-size: 16px;
    font-weight: 300;
    line-height: 1.5;
    color: $grey;
  }

  @include rmin(768) {
    display: flex;
    align-items: center;

    & + & {
      margin-top: 80px;
    }

    &__pic {
      width: 40%;
    }

    &__descr {
      font-size: 24px;
      width: 60%;
      padding-left: 10%;
      padding-right: 0;
    }

    &_reverse {
      .about {
        &__pic {
          order: 2;
        }
  
        &__descr {
          padding-left: 0;
          padding-right: 10%;
        }
      }
    }
  }

  @include rmin(1024) {
    &__pic {
      position: relative;
      overflow: hidden;

      &:after {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        background-color: #fff;
        position: absolute;
        left: 0;
        top: 0;
        transition: transform 1s;
      }

      img {
        transform: translateY(10%);
        transition: transform 1s;
      }
    }

    &__descr {
      opacity: 0;
      transition: opacity 1.3s;
    }

    &.is-active {
      .about {
        &__pic {
  
          &:after {
            transform: translateY(100%);
          }
    
          img {
            transform: translateY(0%);
          }
        }

        &__descr {
          opacity: 1;
        }
      }
    }
  }
}

.papper-langs {
  max-width: 712px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;

  &__item {
    width: 100%;
    padding: 3px;
  }

  &__link {
    display: flex;
    align-items: center;
    font-size: 16px;
    color: $blue;
    padding: 20px;
    border-radius: 4px;
    background-color: rgba(255, 255, 255, 0.051);
    box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.15);
    text-decoration: none;
    transition: background-color .3s, color .3s;

    &:hover {
      background-color: rgba(255, 255, 255, 0.1);
      color: #fff;
    }

    img {
      max-width: 45px;
    }
  }

  &__title {
    padding-left: 20px;
  }

  @include rmin(375) {
    &__item {
      width: 50%;
    }
  }

  @include rmin(768) {
    &__item {
      width: 33.333%;
    }
  }
}

.roadmap {

  &__item {
    position: relative;

    & + & {
      margin-top: 25px;

      &:before {
        content: '';
        display: block;
        height: 30px;
        width: 0px;
        border: 1px dashed $blue;
        position: absolute;
        left: 50%;
        top: -35px;
        transform: translateX(-50%);
      }
    }
  }

  &__icon {
    display: block;
    width: 40px;
    height: 40px;
    border: 2px solid $blue;
    border-radius: 50%;
    background-color: #fff;
    position: relative;

    svg {
      max-width: 80%;
      position: absolute;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      fill: $blue;
    }

    &_red {
      border-color: #ff5d5d;

      svg {
        fill: #ff5d5d;
      }
    }
  }

  &__info {
    text-align: center;
  }

  &__icon {
    margin: 10px auto;
  }

  &__title {
    font-size: 14px;
    color: $blue;
    font-weight: 500;
  }

  &__descr {
    font-size: 12px;
    color: #6c819a;
    font-weight: 500;
    height: 30px;
  }

  @include rmin(768) {
    display: flex;
    justify-content: space-between;
    max-width: 710px;
    height: 290px;
    margin: 0 auto;
    background-image: url(../img/roadmap-bg.png);
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: calc(100% - 80px);

    &__item {
      display: flex;
      align-items: flex-end;
      width: 14.285%;
      text-align: center;

      & + & {
        margin-top: 0;

        &:before {
          display: none;
        }
      }
  
      &:nth-child(2n) {
        align-items: flex-start;
  
        .roadmap {
          &__icon {
            order: 3;
          }
        }
      }
    }

    &__info {
      width: 100%;
      display: flex;
      flex-direction: column;
    }
  }

  @include rmin(1024) {
    &__icon {
      opacity: 0;
      transition: opacity .5s;
    }

    &__item {
      &:nth-child(2n) {
        .roadmap__title {
          opacity: 0;
          transform: translateY(-20px);
          transition: transform .7s, opacity .7s;
          transition-delay: .3s;
        }
      
        .roadmap__descr {
          opacity: 0;
          transform: translateY(-20px);
          transition: transform .7s, opacity .7s;
        }
      }

      &:nth-child(2n-1) {
        .roadmap__title {
          opacity: 0;
          transform: translateY(20px);
          transition: transform .7s, opacity .7s;
        }
      
        .roadmap__descr {
          opacity: 0;
          transform: translateY(20px);
          transition: transform .7s, opacity .7s;
          transition-delay: .3s;
        }
      }

      &.is-active {
        .roadmap {
          &__icon {
            opacity: 1;
          }

          &__title {
            opacity: 1;
            transform: translateY(0);
          }

          &__descr {
            opacity: 1;
            transform: translateY(0);
          }
        }
      }
    }
  }
}

.token-title {
  font-size: 24px;
  font-weight: 300;
  color: #fff;
  text-align: center;
  margin-top: 40px;
  margin-bottom: 20px;

  @include rmin(768) {
    margin-top: 80px;
  margin-bottom: 40px;
  }
}

.table {
  display: flex;
  &__row {
    flex: 1 1;

    &:nth-child(2n) {
      .table__cell {
        background-color: #4a586b;
      }
    }
  }

  &__cell {
    min-height: 60px;
    padding: 0 15px;
    margin: 1px;
    background-color: #4e5c6e;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__cell-title {
    font-size: 14px;
    font-weight: 300;
    color: #b3bbc8;
    margin-bottom: 5px;
  }

  &__cell-text {
    font-size: 14px;
    color: $blue;

    &_white {
      color: #fff;
    }
  }

  @include rmin(768) {
    display: block;

    &__row {
      display: flex;
    }

    &__cell {
      width: 20%;

      &_ta_center {
        text-align: center;
      }
    }
  }
  
  @include rmin(1024) {

    &__cell-text {
      font-size: 18px;
    }
  }
}

.diagram {
  text-align: center;
  margin-top: 50px;

  @include rmin(768) {
    margin-top: 100px;
  }
}

.slider {
  position: relative;


  .slick-track{
    display: flex;
    align-items: stretch;
  }

  &__slide {
    padding: 10px;
    height: auto;
  }

  &__btn {
    font-size: 0;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 50px;
    height: 50px;
    background-color: transparent;
    display: none !important;

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: 100%;
      height: 100%;
      background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20viewBox%3D%220%200%20512%20512%22%20enable-background%3D%22new%200%200%20512%20512%22%3E%3Cpath%20fill%3D%27%23a7a3a1%27%20d%3D%22M367.954%20213.588l-207.284-207.716c-7.804-7.819-20.467-7.831-28.284-.029-7.819%207.802-7.832%2020.465-.03%2028.284l207.299%20207.731c7.798%207.798%207.798%2020.486-.015%2028.299l-207.284%20207.716c-7.802%207.819-7.789%2020.482.03%2028.284%203.903%203.896%209.016%205.843%2014.127%205.843%205.125%200%2010.25-1.958%2014.157-5.873l207.269-207.701c23.394-23.394%2023.394-61.459.015-84.838z%22/%3E%3C/svg%3E');
      background-repeat: no-repeat;
      transform-origin: 50% 50%;
      transition: background-image .3s;
    }

    &:hover {
      &:after {
        background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20viewBox%3D%220%200%20512%20512%22%20enable-background%3D%22new%200%200%20512%20512%22%3E%3Cpath%20fill%3D%27%2300b1ce%27%20d%3D%22M367.954%20213.588l-207.284-207.716c-7.804-7.819-20.467-7.831-28.284-.029-7.819%207.802-7.832%2020.465-.03%2028.284l207.299%20207.731c7.798%207.798%207.798%2020.486-.015%2028.299l-207.284%20207.716c-7.802%207.819-7.789%2020.482.03%2028.284%203.903%203.896%209.016%205.843%2014.127%205.843%205.125%200%2010.25-1.958%2014.157-5.873l207.269-207.701c23.394-23.394%2023.394-61.459.015-84.838z%22/%3E%3C/svg%3E');
      }
    }

    &_prev {
      left: 0px;

      &:after {
        transform: rotate(180deg);
      }
    }

    &_next {
      right: 0px;
    }
  }

  @include rmin(1024) {
    &__btn {
      display: block !important;
    }

    &__content {
      padding: 0 50px;
    }
  }

  @include rmin(1366) {
    margin: 0 -50px;
  }
}

.person-card {
  box-shadow: 0 4px 10px 0 rgba(0,0,0,.1);
  height: 100%;

  &__heading {
    position: relative;
    height: 200px;

    &_h_180 {
      height: 180px;
    }

    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }

  &__link {
    display: block;
    width: 24px;
    height: 24px;
    position: absolute;
