  &__content {
    padding: 30px 20px;
    text-align: center;
    background-color: #fff;
  }

  &__title {
    font-size: 24px;
    font-weight: 300;
  }

  &__position {
    font-size: 16px;
    font-weight: 500;
    color: $blue;
    margin-top: 5px;
  }

  &__descr {
    font-size: 12px;
    margin-top: 25px;

    span {
      display: block;

      & + span {
        margin-top: 5px;
      }
    }
  }
}

.notice{
  position: absolute;
  left: 0;
  bottom: 0;
  transform: translateY(0);
  opacity: 0;
  overflow: hidden;
  background-color: $dark-blue;
  width: 100%;
  padding: 10px;
  display: none;
  @include rmin(1024) {
    display: block;
  };
  &__inner {
    transform: translateY(-200%);
    transition: .3s ease-in;
    text-align: center;
  }
  span {
    color: $yellow;
    font-weight: 500;
    &.underlined {
      text-decoration: underline;
      font-weight: 700;
      .icon {
        fill: $yellow;
        vertical-align: middle;
        margin-left: 10px;
      }
    }
  }
}

.socials {
  justify-content: flex-end;
  display: flex;
  &__item {
    width: 30px;
    height: 35px;
    opacity: 1;
    transition: .3s ease;
    @include rmin(1060) {
      width: 25px;
      height: 30px;
    };
    @include rmin(1366) {
      width: 30px;
      height: 35px;
    };
    &:hover {
      opacity: .5;
    }
    & + & {
      margin-left: 20px;
      @include rmin(1060) {
        margin-left: 10px;
      };
      @include rmin(1366) {
        margin-left: 20px;
      };
    }
    a {
      width: 100%;
      height: 100%;
      display: block;
      background-size: contain;
      background-position: 50% 50%;
      background-repeat: no-repeat;
    }
  }
}

.footer-controls {
  padding-top: 15px;
  padding-bottom: 15px;
  background-color: $blue;

  &__wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }

  &__buttons {
    text-align: center;
  }

  &__button {
    color: $blue;
    background-color: #fff;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.1);
    width: 260px;
    display: inline-block;
    margin-left: 5px;
    margin-right: 5px;

    &:hover {
      background-color: rgba(255,255,255,.8);
    }

    & + & {
      margin-top: 20px;
    }
  }

  &__social {
    width: 100%;
    text-align: center;
    margin-top: 20px;
  }

  &__social-link {
    display: inline-block;
    width: 20px;
    height: 33px;
    margin: 0 10px;
    font-size: 26px;

    svg {
      fill: #fff;
    }

    &:hover {
      svg {
        fill: rgba(255,255,255,.8)
      }
    }
  }

  @include rmin(350) {
    &__social-link {
      width: 26px;
    }
  }

  @include rmin(1024) {
    &__wrap {
      justify-content: space-between;
    }

    &__social {
      width: auto;
      text-align: right;
      margin-top: 0;
    }

    &__buttons {
      text-align: left;
    }

    &__button {
  
      & + & {
        margin-top: 0;
      }
    }
  }
}
