@font-face {
    font-family: "Roboto";
    src: url("./fonts/Roboto-Thin.woff2") format("woff2"),
         url("./fonts/Roboto-Thin.woff") format("woff"),
         url("./fonts/Roboto-Thin.ttf") format("trutype");
    font-weight: 100;
    font-style: normal;
}

